/* !Basics */
* {
  box-sizing: border-box;
}
body {
    font-family: 'Major Mono Display', monospace;
    font-size: 1em;
    background-image: url(../public/nasaCluster.jpg);
    /* opacity: 150%; */
    background-size: 40vmax;
    padding: 1rem;
  }
/* flex to make life easy */
  .flex-grid{
    display: flex;
  }
  .flex-grid .col {
    flex: 1;
  }
  .nav-link{
    color:#ff8400;
    /* opacity: 110%; */
  }
  #title{
    color: rgb(2, 1, 1);
  }
  /* !navbar styling */
#ie4 {
    color: #F8F8FF;
    font-size: 2.7em;
    font-weight: bolder;
    list-style: none;
    background-color: #305040;
    border-radius: 0;
    border: 0;
    opacity: 60%;
    margin-bottom: 20px;
    padding: 0 1rem;
  }
  #ie4 ul{
    display: flex;
    gap: 1rem;
  }
  #ie4 a{
    text-decoration: none;
    margin: 0.5rem;
    padding: 0.5em;
  }

  #landingPage{
    font-size: 0.9em;
    /* font-family: 'Major Mono Display', monospace; */
    font-weight: bolder;
    color: #ff9914;
    /* background-color: #abc3e2; */
    font-weight: 1444;
    margin-bottom: 1rem;
    padding: 0 2em;
  }
  #revealer{
    font-family: 'Major Mono Display', monospace;
    font-weight: 1444;
    color: #ff9914;
    /* background-color: #b68a5c; */
    margin-top: 1rem;
  }

  footer{
    /* font-family: 'Major Mono Display', monospace; */
    color: #ff9914;
    font-weight: 1404;
    /* background-color: #b68a5c; */
    margin-top: 20px;
    font-size: 0.8em;
  }
  input{
    /* font-family: 'Major Mono Display', monospace; */
    color: #ff9914;
    font-weight: 1000;
    margin-top: 20px;
  }
  #pictureRef a{
    color: #ff9914;
  }
div > p{
  opacity: 200%;
  font-weight: bolder;
}